* {
  margin: 0;
  padding: 0;
}

.main-container {
  width: 100vw;
  height: 100vh;
  background-color: aquamarine;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.display {
  background-color: white;
  width: 80vw;
  height: 90vh;
}
.display h1 {
  background-color: rgb(29, 104, 79);
  padding: 10px;
  color: white;
  text-align: center;
}

.choice {
  width: 26.7vw;
  height: 5vh;
  margin: -1px;
  border: none;
  cursor: pointer;
  background-color: rgb(39, 182, 110);
  font-size: 20px;
  color: white;
  font-weight: 600;
  transition: 0.3s;
}
.choice:hover {
  transform: scale(0.95);
  background-color: rgb(25, 138, 81);
}

.playerSide {
  display: flex;
  align-items: center;
}

.computerSide {
  display: flex;
  align-items: center;
}

.playerChoiceText {
  font-size: 30px;
}
.playerChoiceImage img {
  width: 20vw;
  margin-top: -200px;
}

.computerChoiceImage img {
  width: 20vw;
  margin-top: -200px;
  transform: rotate(180deg);
}

.Computer {
  margin-left: 600px;
}

.mainDisplay {
  display: flex;
  height: 100%;
  justify-content: space-evenly;
  align-items: center;
}

.statistic {
  display: flex;
  justify-content: space-evenly;
  font-size: 30px;
  background-color: green;
  color: white;
}

.choices {
  display: flex;
  justify-content: space-evenly;
  margin-top: 100px;
}

.resetButton {
  width: 100%;
  height: 50px;
  cursor: pointer;
  background-color: rgb(6, 80, 6);
  border: none;
  color: white;
  font-size: 30px;
}
